import './ScroolbarStyle.scss';

export default class Example extends Component {
  componentDidMount() {
      this.setState({ onXReachEnd: () => console.log() });
  }

  render() {
    // const {show, text} = this.props
    return (
      <ScrollBar className={'AppScroolbar'}>
        {this.props.children}
      </ScrollBar>
    )
  }
}